import React,{Component} from 'react'
import '../css/Detail.css'
export default class Detail extends Component {
  constructor() {
    super();
    this.state = {
      data:{}
    }
  }


  componentDidMount() {
    // let page = this.props.match.params.id;
    // console.log(this.props.match.params.id);
    // console.log('https://cnodejs.org/api/v1/topic/'+this.props.match.params.id)
    fetch('https://cnodejs.org/api/v1/topic/'+this.props.match.params.id)
      .then((res)=> res.json())
      .then((res)=> {
        // console.log(res.data.content)
        console.log(typeof(res))
        this.setState({
          data:res.data
        })
      })
  }

  render(){
    let {url} = this.props.match
    console.log(this.state.data.content)
    return(
      <div className="detail-container">

        <h2 className="title">{this.state.data.title}</h2>
        <hr/>
        <div className="detail" dangerouslySetInnerHTML={{__html:this.state.data.content}}>
        </div>

      </div>


    )
  }

}
