import React from 'react'
import '../css/Home.css'
import {BrowserRouter as Router,Link,Route,Redirect} from 'react-router-dom'
import All from './All'

export default class Home extends React.Component {
  render() {
    let {url} = this.props.match
    return(
      <div className="home-inner">
        <div className="home-navbar">
          <Link className="home-link" to={`/home`}>全部</Link>
          <Link className="home-link" to={`/home`}>精华</Link>
          <Link className="home-link" to={`/home`}>分享</Link>
          <Link className="home-link" to={`/home`}>问答</Link>
          <Link className="home-link" to={`/home`}>招聘</Link>
          <Link className="home-link" to={`/home`}>客户端测试</Link>
        </div>
        <div>
            <Route exact path='/home' render={()=> (
              <Redirect to="/home/all"/>
            )}/>

            <Route path='/home/all/:id' component={All}/>
            <Route exact path='/home/all' component={All} />
        </div>
      </div>
    )
  }
}
