import React,{ Component } from 'react';
import '../css/Nav.css';
import {BrowserRouter,Link,Route} from 'react-router-dom';
import Login from '../Content/Login.js';
export default class Nav extends Component {
  render() {
    return (
      <div className="header">
        <div className="navbarContainer">
          <a className="brand">
            <img src="https://static2.cnodejs.org/public/images/cnodejs_light.svg"/>
          </a>
          <form className="navbar-search" action="/search">
            <input type="text" name="q" className="search-query">
            </input>
          </form>


          <div className="nav">
            <BrowserRouter>
              <ul>
                <li><Link className="link" to="/home">首页</Link></li>
                <li><Link className="link" to="/about">关于</Link></li>
                <li><Link className="link" to="/login">登录</Link></li>
              </ul>

            </BrowserRouter>
          </div>
        </div>
      </div>

)
}
}
